import axios from '@common/plugins/Axios';

class Privileges {
  /**
   * 数据权限-领域查询
   * @param{String} parentId
   * @param{String} id
   * @return {axios.requestPromise | *}
   */
  gerPrivilegeList(parentId, id) {
    return axios.request({
      url: `/business/privilege`,
      method: 'get',
      params: {
        parentId: parentId,
        id: id,
      }
    })
  }

  /**
   * 数据权限-领域详情
   * @param{String} id
   * @return {axios.requestPromise | *}
   */
  gerPrivilege(id) {
    return axios.request({
      url: `/business/privilege/${id}`,
      method: 'get',
    })
  }

  /**
   * 数据权限-领域新增
   * @param{Object} param
   * @return {axios.requestPromise | *}
   */
  addPrivilege(param) {
    return axios.request({
      url: `/business/privilege`,
      method: 'post',
      data: param
    })
  }

  /**
   * 数据权限-领域修改
   * @param{Object} param
   * @return {axios.requestPromise | *}
   */
  editPrivilege(param) {
    return axios.request({
      url: `/business/privilege`,
      method: 'put',
      data: param
    })
  }

  /**
   * 数据权限-领域删除
   * @param{String} id
   * @return {axios.requesPromise | *}
   */
  deletePrivilege(id){
    return axios.request({
      url: `/business/privilege/${id}`,
      method: 'delete'
    })
  }
}

const privileges = new Privileges();
export default privileges;