import axios from '@common/plugins/Axios';

class Organizations {

  /**
  * 组织列表查询
  * @param{String} parentOrgId
  * @return{Object} Promise
  */
  getOrganizationList(parentOrgId) {
    return axios.request({
      url: `/business/org`,
      method: 'get',
      params: {
        parentOrgId: parentOrgId
      }
    });
  }

  /**
   * 组织查询
   * @param{String} orgId
   * @return{Object} Promise
   */
  getOrganization(orgId) {
    return axios.request({
      url: `/business/org/${orgId}`,
      method: 'get',
    });
  }

  /**
  * 新增组织
  * @param{Object} param
  * @return {axios.requestPromise | *}
  */
  addOrganization(param) {
    return axios.request({
      url: `/business/org`,
      method: 'post',
      data: param
    })
  }

  /**
   * 修改组织
   * @param{Object} param
   * @return {axios.requestPromise | *}
   */
  editOrganization(param) {
    return axios.request({
      url: `/business/org`,
      method: 'put',
      data: param
    })
  }

  /**
   * 删除组织
   * @param{String} orgId
   * @return {axios.requesPromise | *}
   */
  deleteOrganization(orgId){
    return axios.request({
      url: `/business/org/${orgId}`,
      method: 'delete'
    })
  }
}

const organizations = new Organizations();
export default organizations;