import api from '@common/api/index'

export default {
  async initDataPrivileges({commit,  dispatch, state}, payload) {
    let res = await api.auth.getDataPrivileges();
    let dataPrivilegeList = res.data.data || [];
    // dataPrivilegeList = dataPrivilegeList.filter( item => item.id === 2)
    // console.log('数据权限',dataPrivilegeList)
    commit('setPrivilegeList',JSON.stringify(dataPrivilegeList));
    commit('setDataId','');
    if (dataPrivilegeList && dataPrivilegeList.length === 1) {
      let response = await api.auth.getNewJWT(dataPrivilegeList[0].id);
      commit('setDataId',dataPrivilegeList[0].id);
      dispatch('auth/setToken', response.data.data, {root: true});
    }
  }
};