import axios from '@common/plugins/Axios';

class Staffs {
  /**
   * 组织列表查询
   * @return{Object} Promise
   */
  getOrganizationList() {
    return axios.request({
      url: `/business/org`,
      method: 'get',
    });
  }

  /**
   * 员工列表查询
   * @param{Object} param
   * @return{Object} Promise
   */
  getStaffList(param) {
    return axios.request({
      url: `/business/staff`,
      method: 'get',
      params: param
    });
  }

  /**
   * 新增员工
   * @param{Object} param
   * @return {axios.requestPromise | *}
   */
  addStaff(param){
    return axios.request({
      url: `/business/staff`,
      method: 'post',
      data: param
    })
  }

  /**
   * 编辑员工
   * @param{Object} param
   * @return {axios.requestPromise | *}
   */
  editStaff(param) {
    return axios.request({
      url: `/business/staff`,
      method: 'put',
      data: param
    })
  }

  /**
   * 删除员工
   * @param{String} staffId
   * @return {axios.requesPromise | *}
   */
  deleteStaff(staffId) {
    return axios.request({
      url: `/business/staff/${staffId}`,
      method: 'delete'
    })
  }


  /**
   * 数字字典查询
   * @param{String} tableName
   * @param{String} columnName
   * @return{Object} Promise
   */
  getDic(tableName, columnName) {
    return axios.request({
      url: `/common/dictionary`,
      method: 'get',
      params: {
        tableName: tableName,
        columnName: columnName
      }
    });
  }

}

const staffs = new Staffs();
export default staffs;
