import {makeTree} from '@common/util';
import constants from '@common/constants/constants';

export default {
  menus: (state) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    return resources === null ? null : resources.filter((resource) => {
      return resource.resourceType === 'MENU';
    });
  },
  elements: (state) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    return resources === null ? null : resources.filter((resource) => {
      return resource.resourceType === 'ELEMENT';
    });
  },
  resourcesTree: (state) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    return resources === null ? null : makeTree('resourceId', 'pResourceId', resources);
  },
  menusTree: (state) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    return resources === null ? null : (function() {
      const menus = resources.filter((resource) => {
        return resource.resourceType === 'MENU';
      });
      return makeTree('resourceId', 'pResourceId', menus);
    })();
  },
  elementsTree: (state) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    return resources === null ? null : (function() {
      const menus = resources.filter((resource) => {
        return resource.resourceType === 'ELEMENT';
      });
      return makeTree('resourceId', 'pResourceId', menus);
    })();
  },
  /**
   * 获取可进入的第一个菜单的地址，用于登录后重定向
   * @param {Object} state
   * @return {*}
   */
  firstMenuUrl: (state) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    if (resources !== null) {
      let firstMenu = resources.filter((resource) => {
        return resource.resourceUrl !== '' && resource.resourceUrl !== null;
      });
      let indexMenu = resources.filter((resource) => {
        return resource.previlegeCode === constants.INDEX_CODE;
      });

      if (firstMenu.length !== 0) {
        return firstMenu[0].resourceUrl;
      } else if (indexMenu.length !== 0) {
        return indexMenu[0].resourceUrl;
      } else {
        return null;
      }
    } else {
      return null;
    }
  },
  hasRouterPermission: (state) => (path) => {
    let resources = state.resources || JSON.parse(localStorage.getItem(constants.RESOURCES_KEY));
    if (resources === null) {
      return false;
    }
    let hasPermission = resources.filter((e) => {
      return e.resourceType === 'MENU' && e.resourceUrl === path;
    });
    return hasPermission.length > 0 ? true : false;
  }
};

