import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@store/index';
import constants from '@common/constants/constants';
import {toSSO, getURLParameter} from '@common/util'
import {Loading} from 'element-ui'

Vue.use(VueRouter);

let routePaths = {
  main: '/',
  index: '/index',
  userSection: '/view/user/index',
  roleSection: '/view/role/index',
  resourceSection: '/view/resource/index',
  organizationSection: '/view/organization/index',
  staffSection: '/view/staff/index',
  privilegeSection: '/view/privilege/index',
  clientSection: '/view/client/index'
}
/**
 * 创建一个新路由
 * @return {VueRouter}
 */
const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      name: 'main',
      path: routePaths.main,
      meta: {
        requireAuth: true
      },
      component: (r) => require(['@components/layouts/MainSection'], r),
      children: [
        {
          name: 'index',
          path: routePaths.index,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/IndexSection'], r),
        },
        {
          name: 'user',
          path: routePaths.userSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/users/UserSection'], r),
        },
        {
          name: 'client',
          path: routePaths.clientSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/clients/ClientSection'], r),
        },
        {
          name: 'organization',
          path: routePaths.organizationSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/organizations/OrganizationSection'], r),
        },
        {
          name: 'staff',
          path: routePaths.staffSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/staffs/StaffSection'], r),
        },
        {
          name: 'privilege',
          path: routePaths.privilegeSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/privileges/PrivilegeSection'], r),
        },
        {
          name: 'role',
          path: routePaths.roleSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/roles/RoleSection'], r),
        },
        {
          name: 'resource',
          path: routePaths.resourceSection,
          meta: {
            requireAuth: true
          },
          component: (r) => require(['@components/resources/ResourceSection'], r),
        }
      ]
    },
    {
      name: 'login',
      path: '/login',
      meta: {
        requireAuth: false
      },
      component: (r) => require(['@components/LoginSection'], r),
    },
    {
      path: '/logout',
      name: 'logout',
      meta: {
        requireAuth: false
      }
    },
    {
      name: 'dataprivilege',
      path: '/privilege',
      props: route => ({route: route}),
      meta: {
        requireAuth: true
      },
      component: (r) => require(['@components/PrivilegeChoose'], r),
    },
    {
      name: 'error',
      path: '/auth/error',
      props: route => ({errorCode: route.query.errorCode}),
      meta: {
        requireAuth: false
      },
      component: (r) => require(['@components/layouts/Error'], r),
    }]
});


/**
 * 全局路由钩子
 */
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem(constants.TOKEN_KEY)
  const dataId = localStorage.getItem('dataId')
  const privilegeList = JSON.parse(localStorage.getItem('privilegeList'))
  // console.log('token',token)
  if (token === null || token === 'undefined' || token === undefined) {
    // 执行用户登录认证前增加加载中遮罩
    const loading = Loading.service({
      fullscreen: true,
      text: '加载系统配置，请稍等'
    })
    // router history 模式获取code
    let code = to.query.code
    // router hash模式获取code
    code = code || getURLParameter('code')
    if (code && code.indexOf('#') !== -1) {
      code = code.substring(0, code.indexOf('#'))
    }
    // 认证成功获取accessToken或跳转到认证页面
    code
      ? store.dispatch('auth/initJwtToken', {code}).then(async function (res) {
        // 认证完成关闭遮罩
        loading.close();
        store.dispatch('privilege/initDataPrivileges').then( () => {
          if(privilegeList && privilegeList.length > 1){
            next('/privilege');
          }else {
            next('/index');
          }
        })
      })
      : toSSO()
  } else {
    if ( dataId === '' && privilegeList && privilegeList.length > 1 && (to.path === '/' || to.path === '/index')) {
      next('/privilege');
      return;
    }
    if (
      to.path === '/auth/error' ||
      to.path === '/' ||
      to.path === '/index' ||
      to.path === '/privilege'
    ) {
      next();
      return;
    }
    let errorCode = 200
    let pathExists = false
    // 判断是否合法路由
    for (let path in routePaths) {
      if (routePaths[path] === to.path) {
        pathExists = true
        break
      }
    }
    if (pathExists) {
      // 判断是否授权路由
      errorCode = store.getters['auth/hasRouterPermission'](to.path) ? 200 : 403
    } else {
      errorCode = 404
    }
    errorCode === 200 ? next() : next('/auth/error?errorCode=' + errorCode)
  }
});

/**
 * 路由导航异常handler
 */
router.onError(function (error) {
  console.error(error);
  console.log('导航出错了');
});

export default router;

