import Constants from '@common/constants/constants';

export default {
  setUser(state, payload) {
    localStorage.setItem(Constants.USER_KEY, JSON.stringify(payload.user));
    state.user = payload.user;
  },
  setToken(state, payload) {
    localStorage.setItem(Constants.TOKEN_KEY, payload.token);
    state.token = payload.token;
  },
  setAccessToken (state, payload) {
    localStorage.setItem(Constants.ACCESS_TOKEN_KEY, payload.accessToken)
    state.accessToken = payload.accessToken
  },
  setRoles(state, payload) {
    localStorage.setItem(Constants.ROLES_KEY, JSON.stringify(payload.roles));
    state.roles = payload.roles;
  },
  setResources(state, payload) {
    localStorage.setItem(Constants.RESOURCES_KEY, JSON.stringify(payload.resources));
    state.resources = payload.resources;
  },
  clear(state) {
    localStorage.removeItem(Constants.USER_KEY);
    localStorage.removeItem(Constants.TOKEN_KEY);
    localStorage.removeItem(Constants.RESOURCES_KEY);
    localStorage.removeItem(Constants.ROLES_KEY);
    state.user = null;
    state.roles = null;
    state.token = null;
    state.resources = null;
  }
};
