import axios from '@common/plugins/Axios';
import config from '@/config';

/**
 * 登录鉴权模块接口集合
 */
class Auth {
  /**
   * 构造器
   */
  constructor() {
  }

  /**
   * 获取数据权限列表
   * @return {AxiosPromise | *}
   */
  getDataPrivileges() {
    return axios.request({
      url: `/auth/dataPrivileges`,
      method: 'get'
    })
  }

  /**
   * 刷新jwt
   * @param{String} dataId
   * @return {axios.requestPromise | *}
   */
  getNewJWT(dataId) {
    return axios.request({
      url: `/authorization/jwt/${dataId}`,
      method: 'put',
    })
  }



  /**
   * 获取角色列表
   * @param {String} loginAccount
   * @return {AxiosPromise | *}
   */
  fetchRoles({loginAccount}) {
    return axios.request({
      url: `/auth/roles`,
      method: 'get',
      params: {
        loginAccount,
        clientCode: config.clientCode
      }
    });
  }

  /**
   * 获取权限列表
   * @param {String} loginAccount
   * @return {AxiosPromise | *}
   */
  fetchPrivileges({loginAccount}) {
    return axios.request({
      url: `/auth/privileges`,
      method: 'get',
      params: {
        loginAccount,
        clientCode: config.clientCode
      }
    });
  }


  /**
   * 根据code 获取token接口
   * @param code
   * @returns {*}
   */
  getJwtToken (code) {
    return axios.request({
      url: `/authorization/token`,
      method: 'get',
      params: {
        code
      }
    })
  }

  getUser () {
    return axios.request({
      url: `/authorization/user`,
      method: 'get'
    })
  }

  singleSign (params) {
    return axios.request({
      url: `/ssoAuthorize/exchange`,
      method: 'get',
      params: params
    })
  }
}
const auth = new Auth();
export default auth;
